import React, { useContext, useEffect, useMemo, useState } from 'react';
import { Button, Container, makeStyles, MenuItem, TextField, Typography } from '@material-ui/core';
//React helmet
import { Helmet } from 'react-helmet';

import useLocationLevel from '../hooks/useLocationLevel';
import AlertContext from '../config/AlertContext';
import UserContext from '../config/UserContext';
import axiosInstance from '../utils/axiosInstance';
import handleAxiosErrors from '../utils/axiosErrorHandler';
import { root, shortQuiz } from '../utils/fixedRoutes';

import { levelsArray, schoolDeptArray, jobsDeptArray } from '../utils/optionsArrays';

export default function ShortQuiz() {
	const classes = useStyles();
	const running = useLocationLevel(1, root);

	const {
		setAlertMessage,
		setAlertSeverity,
		startCountdownTimer,
		setCountdownLink,
		setIsCountdownOpen,
	} = useContext(AlertContext);
	const { user } = useContext(UserContext);

	const [step, setStep] = useState(1);

	const [title, setTitle] = useState('');
	const [questions, setQuestions] = useState([]);

	const [level, setLevel] = useState('');
	const [subject, setSubject] = useState('');
	const [department, setDepartment] = useState('');
	const [departmentsArray, setDepartmentsArray] = useState([]);

	useEffect(() => {
		let isMounted = true;
		if (isMounted && running === 'running') {
			axiosInstance({ method: 'get', url: '/quizzes/random' })
				.then(function (response) {
					setCountdownLink(`${shortQuiz}/running`);
					setIsCountdownOpen(true);
					setQuestions(response.data.data.questions);
					setTitle(response.data.data.title);
					setLevel(response.data.data.class);
					setDepartment(response.data.data.department);
					setSubject(response.data.data.subject);
					setStep(2);
					startCountdownTimer(response.data.data.timeOut);
				})
				.catch(function (error) {
					handleAxiosErrors(error, setAlertMessage, setAlertSeverity);
				});
		}

		return () => (isMounted = false);
	}, [running]);

	useEffect(() => {
		let isMounted = true;
		if (isMounted) {
			if (level === 'JSC' || level === 'SSC' || level === 'HSC' || level === 'Admission') {
				setDepartmentsArray(schoolDeptArray);
			} else {
				setDepartmentsArray(jobsDeptArray);
			}
			setDepartment('');
		}
		return () => (isMounted = false);
	}, [level]);

	const levelsMenu = useMemo(() => {
		return levelsArray.map((option) => (
			<MenuItem key={option} value={option}>
				{option ? option : 'All'}
			</MenuItem>
		));
	}, []);

	const departmentsMenu = useMemo(() => {
		return departmentsArray.map((option) => (
			<MenuItem key={option} value={option}>
				{option ? option : 'All'}
			</MenuItem>
		));
	}, [departmentsArray]);

	const step1 = useMemo(() => {
		if (step === 1) {
			return (
				<div className={classes.paper}>
					<Typography
						variant='subtitle1'
						color='primary'
						className={classes.marginBottom}>
						{Boolean(user.userName)
							? 'Start new quiz'
							: 'You need to log in to take a quiz'}
					</Typography>

					<TextField
						onChange={(event) => setLevel(event.target.value)}
						value={level}
						label='Level'
						select
						variant='outlined'
						margin='dense'
						fullWidth
						className={classes.marginBottom}>
						{levelsMenu}
					</TextField>

					<TextField
						onChange={(event) => setDepartment(event.target.value)}
						value={department}
						label='Department'
						select
						variant='outlined'
						margin='dense'
						fullWidth
						className={classes.marginBottom}>
						{departmentsMenu}
					</TextField>

					<TextField
						onChange={(event) => setSubject(event.target.value)}
						inputProps={{ maxLength: '30' }}
						value={subject}
						margin='dense'
						label='Subject'
						variant='outlined'
						fullWidth
						className={classes.marginBottom}
					/>

					<Button
						onClick={() => {
							setAlertSeverity('info');
							setAlertMessage('Starting...');

							let levelQuery, departmentQuery, subjectQuery;
							if (level) levelQuery = `class[eq]=${level}`;
							if (department) departmentQuery = `department[eq]=${department}`;
							if (!level) departmentQuery = '';
							if (subject) subjectQuery = `subject[eq]=${subject}`;
							const query = `${levelQuery}&${departmentQuery}&${subjectQuery}`;

							axiosInstance({ method: 'get', url: `/quizzes/random?${query}` })
								.then(function (response) {
									setCountdownLink(`${shortQuiz}/running`);
									setIsCountdownOpen(true);
									setQuestions(response.data.data.questions);
									setTitle(response.data.data.title);
									setLevel(response.data.data.class);
									setDepartment(response.data.data.department);
									setSubject(response.data.data.subject);
									setStep(2);
									startCountdownTimer(response.data.data.timeOut);

									setAlertMessage('');
									if (response.data.message) {
										setAlertSeverity('warning');
										setAlertMessage(response.data.message);
									}
								})
								.catch(function (error) {
									handleAxiosErrors(error, setAlertMessage, setAlertSeverity);
								});
						}}
						color='primary'
						size='large'
						variant='contained'
						disabled={!Boolean(user.userName)}
						className={classes.startButton}>
						Start
					</Button>
				</div>
			);
		}

		return undefined;
	}, [step, classes, user, level, levelsMenu, department, departmentsMenu, subject]);

	const step2 = useMemo(() => {
		if (step === 2) {
			return (
				<React.Fragment>
					<Typography>Quiz</Typography>
				</React.Fragment>
			);
		}

		return undefined;
	}, [step, classes, level, department, subject]);

	const step3 = useMemo(() => {
		if (step === 3) {
			return (
				<React.Fragment>
					<Typography>Result</Typography>
				</React.Fragment>
			);
		}

		return undefined;
	}, [step, classes, level, department, subject]);

	return (
		<React.Fragment>
			<Helmet>
				<title>{`Short Quiz - ELearnBD`}</title>
			</Helmet>

			<Container maxWidth='xl'>
				{step1}

				{step2}

				{step3}

				{/* <iframe
				src={`${process.env.PUBLIC_URL}/quiz`}
				title='short-quiz-iframe'
				style={{ border: 'none', width: '100%', height: 540 }}
			/> */}
			</Container>
		</React.Fragment>
	);
}

const useStyles = makeStyles((theme) => ({
	paper: {
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'flex-start',
		justifyContent: 'center',
		padding: theme.spacing(2),
		color: theme.palette.text.primary,
		margin: '24px auto',
		backgroundColor: theme.palette.background.paper,
		borderRadius: theme.shape.borderRadius,
		boxShadow: theme.shadows[2],
	},

	marginBottom: {
		marginBottom: theme.spacing(1),
	},

	startButton: {
		minWidth: 120,
		margin: '8px 0px 0px auto',
	},
}));
